@import '../../styles/var.sass';

.block_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .img_block {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 45%;
    // width: 200px
    height: 35vw;
    text-align: center;
    margin: 20px 0px;
    padding: 10px;
    background-color: $gray;
    border-radius: 10px;
    cursor: pointer;
    box-sizing: border-box;
    .svg {
      width: 60px;
      height: 60px;
      fill: $icon-black; }
    .img_text {
      font-size: 0.85rem; }
    &:hover {
      background-color: $green;
      color: $icon-white;
      .svg {
        fill: $icon-white; } } } }
.body_actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
  .button {
    display: block; } }
